<template>
  <div>
    <demo-block title="基础" :card="true">
      <gw-button size="large" type="default" @click="onPopupShow('center')" >center</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('left')">left</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('right')">right</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('top')">top</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('bottom')">bottom</gw-button>
    </demo-block>
    <gw-popup
      :show="show"
      :position="position"
      :mask="true"
      :overlay="true"
      @clickOverlay="onClickOverlay"
    >
      <div :class="['popup-content', position]">
        内容
      </div>
    </gw-popup>
  </div>
</template>

<script>
import { Button, Popup } from 'gw-vue-ui'
export default {
  components: {
    GwButton: Button,
    GwPopup: Popup
  },
  data () {
    return {
      show: false,
      position: 'center'
    }
  },
  methods: {
    onPopupShow (position) {
      this.position = position
      this.show = true
    },
    onClickOverlay () {
      this.show = false
    }
  }
}
</script>

<style lang="scss" scoped>
.gw-button {
  margin-bottom: 10px;
}
.popup-content {
  width: 100px;
  height: 100px;
  background: #fff;
  &.top,
  &.bottom {
    width: 100%;
    height: 100px;
  }
  &.left,
  &.right {
    width: 100px;
  }
}
</style>
